import React from 'react'

const AdjectiveSize=()=>{
    return(
        <div className="gadject-container">
            <div className="gadject-head">Adjectives of shape and size:</div>

            <div className="shape-container">

                <div className="shape">
                    <div className='table1'>
                        <p>big</p>
                        <p>small</p>
                        <p>long</p>
                        <p>short</p>
                        <p>narrow</p>
                        <p>wide</p>
                        <p>thick</p>
                        <p>thin</p>
                    </div>

                    <div className='table1'>
                        <p>l</p>
                        <p>kker</p>
                        <p>gn</p>
                        <p>kkr</p>
                        <p>hh</p>
                        <p>gbr</p>
                        <p>npn</p>
                        <p>ttrn</p>
                    </div>
                </div>

                <div className="shape">
                    <div className='table1'>
                        <p>heavy</p>
                        <p>light</p>
                        <p>old</p>
                        <p>young</p>
                        <p>tall</p>
                        <p>round</p>
                        <p>fat</p>
                        <p>smooth</p>
                    </div>

                    <div className='table1'>
                        <p>wwo</p>
                        <p>flfl</p>
                        <p>argb</p>
                        <p>d</p>
                        <p>ga</p>
                        <p>rbt</p>
                        <p>sanra</p>
                        <p>ddn</p>
                    </div>
                </div>
            </div>
        
        </div>

    )
}

